$prime: #00FF80;
$second: #0c1016;

body,
html {
    background-color: $second; }

svg {
    position: absolute;
    top: 5%;
    left: 50%;
    margin-left: -155px;
    text-align: center;
    vertical-align: middle; }

#gooey-button {
    padding: 1rem;
    font-size: 2rem;
    border: none;
    color: $second;
    filter: url('#gooey');
    position: absolute;
    left: 50%;
    margin-left: -90px;
    top: 1%;
    background-color: $prime;

    &:focus {
        outline: none; }

    .bubbles {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;

        .bubble {
            background-color: $prime;
            border-radius: 100%;
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            z-index: -1;

            @for $bubble from 1 through 10 {
                &:nth-child(#{$bubble}) {
                    $size: 25px;
                    left: (random(90) + 10)+px;
                    width: $size;
                    height: $size;
                    animation: move-#{$bubble} #{3 + $bubble*0.02}s infinite;
                    animation-delay: #{$bubble*0.2}s; } } } } }

@for $bubble from 1 through 10 {
    @keyframes move-#{$bubble} {
        0% {
            transform: translate(0, 0); }
        99% {
            transform: translate(0, -(random(80) + 50)+px); }
        100% {
            transform: translate(0, 0);
            opacity: 0; } } }





td {
  border: 2px solid #333;
  height: 100px;
  width: 100px;
  text-align: center;
  vertical-align: middle;
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-size: 70px;
  cursor: pointer; }

table {
  border-collapse: collapse;
  position: absolute;
  left: 50%;
  margin-left: -155px;
  top: 20%;

  tr {
    &:first-child td {
      border-top: 0; }

    &:last-child td {
      border-bottom: 0; }

    td {
      &:first-child {
        border-left: 0; }

      &:last-child {
        border-right: 0; } } } }

.endgame {
  display: none;
  width: 200px;
  top: 120px;
  background-color: rgba(205, 133, 63, 0.8);
  position: absolute;
  left: 50%;
  margin-left: -100px;
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
  border-radius: 5px;
  color: white;
  font-size: 2em; }

.ReloadButton {
  position: absolute;
  left: 50%;
  margin-left: -40px;
  top: 10%; }






